import React, {useEffect, useState} from 'react';
import {AiOutlineSearch} from "@react-icons/all-files/ai/AiOutlineSearch";
import {connect} from 'react-redux'
import {setWeatherData,setTime} from "../redux/Reducers/Reducers";
import axios from "axios";
import ApiKey from "../constants/ApiKey";

const WeatherRight = ({weatherData,setWeatherData,setTime}) => {
    const [regions,setRegions]=useState([
        {
            "id":1,
            "region":"Tashkent"
        },
        {
            "id":2,
            "region":"Andijon"
        },
        {
            "id":3,
            "region":"Buxoro"
        },
        {
            "id":4,
            "region":"Fargʻona"
        },
        {
            "id":5,
            "region":"Jizzax"
        },
        {
            "id":6,
            "region":"Khorezm"
        },
        {
            "id":7,
            "region":"Namangan"
        },
        {
            "id":8,
            "region":"Navoiy"
        },
        {
            "id":9,
            "region":"Qashqadaryo"
        },
        {
            "id":10,
            "region":"Qoraqalpogʻiston"
        },
        {
            "id":11,
            "region":"Samarqand"
        },
        {
            "id":12,
            "region":"Sirdaryo"
        },
        {
            "id":13,
            "region":"Surkhandarya"
        },
        {
            "id":14,
            "region":"Toshkent"
        }

    ])
    const time = null;
    const [ctime,setDate] = useState(time);
    const URLdata='https://api.openweathermap.org/data/2.5/weather'
    const [query,setQuery]=useState('Tashkent')
    const [active,setActive]=useState(0)
    const isActive = (id) => {
      setActive(id)
    }
    const search =  (e) => {
        if (e.key==='Enter'){
            let time = new Date().toLocaleTimeString();
            setTime(time);
             axios.get(URLdata,{
                params:{
                    q:query,
                    units:'metric',
                    APPID:ApiKey
                },

            })
                 .then((res)=>{
                     setWeatherData(res)
                 })
        }
    }
    const buttonClick = (name) => {
        let time = new Date().toLocaleTimeString();
        setTime(time);
        axios.get(URLdata,{
            params:{
                q:name,
                units:'metric',
                APPID:ApiKey
            },

        })
            .then((res)=>{
                setWeatherData(res)
            })
    }
    const searchRightButton = () => {
        let time = new Date().toLocaleTimeString();
        setTime(time);
        axios.get(URLdata,{
            params:{
                q:query,
                units:'metric',
                APPID:ApiKey
            },

        })
            .then((res)=>{
                setWeatherData(res)
            })
    }
    return (
        <section className="weather-right">
            <div className="search-div">
                <input
                    onChange={(e)=>setQuery(e.target.value)}
                    value={query}
                    onKeyPress={search}
                     type="text"/>
                <button onClick={()=>{
                    searchRightButton()
                }}><AiOutlineSearch/></button>
            </div>
            <div className="regions">
                <ul>
                    {regions.map((item,index)=>{
                        return(
                            <li key={index.toString()}>
                                <button
                                    className={index===active?'active':''}
                                    onClick={()=>{
                                buttonClick(item.region)
                                isActive(index)
                            }}>{item.region}</button></li>
                        )
                    })}
                </ul>
            </div>
            {weatherData.data && <div className="weather-details">
                <span className="title">Weather Details</span>
                <div>
                    <h6>Cloudy</h6>
                    <span>12%</span>
                </div>
                <div>
                    <h6>Humidity</h6>
                    <span>{weatherData.data.main.humidity}%</span>
                </div>
                <div>
                    <h6>Wind</h6>
                    <span>{weatherData.data.wind.speed}km/h</span>
                </div>
                <div>
                    <h6>Pressure</h6>
                    <span>{weatherData.data.main.pressure} hPa</span>
                </div>
            </div>}
        </section>
    );
};
const mapStateToProps = ({ reducer: { weatherData } }) => ({ weatherData })
export default connect(mapStateToProps,{setWeatherData,setTime})(WeatherRight);
