import React from 'react';
import sunny from '../assets/images/sunny.png'
import {WiDaySunny} from "@react-icons/all-files/wi/WiDaySunny";
import {connect} from "react-redux";
import moment from "moment";
import {useSelector} from "react-redux";
const WeatherLeft = ({weatherData}) => {
    const time=useSelector(state => state.time)
    return (

            <>
                {weatherData.data ?
                    <div className="weather-left">
                        <h6>the.weather</h6>
                        <img src={`http://openweathermap.org/img/wn/${weatherData.data.weather[0].icon}@2x.png`} alt=""/>
                        <div className="weather-info">
                            <h1>
                                {weatherData.data.main.temp}
                                <span className="degree">0</span>
                            </h1>

                            <div className="area">
                                <span>{weatherData.data.name} ({weatherData.data.sys.country})</span>
                                <span>{moment(time).format("DD.MM.YYYY HH:MM")}</span>
                                {/*<span>10:36-Tuesday,22 Oct `19</span>*/}
                            </div>
                            <div className="weather-icon">
                                <img src={`http://openweathermap.org/img/wn/${weatherData.data.weather[0].icon}.png`} alt=""/>
                                <span>{weatherData.data.weather[0].main}</span>
                            </div>
                        </div>
                    </div>
                    :
                    <h1 style={{fontSize:"50px",width:"100%",textAlign:"center"}}>Biron bir shahrni qidirganingizdan so'ng ma'lumotlar chiqadi</h1>
                }
            </>

    );
};
const mapStateToProps = ({ reducer: { weatherData } }) => ({ weatherData })
export default connect(mapStateToProps) (WeatherLeft);
